<template>
  <div class="game">
    <div class="chart" ref="chartEl" />
    <van-row gutter="26" class="op-box">
      <van-col span="12">
        <van-button block type="danger" :disabled="isClick" @click="buy(0)">➘ 估降</van-button>
      </van-col>
      <van-col span="12">
        <van-button block type="primary" :disabled="isClick" @click="buy(2)">➚ 估升</van-button>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import echarts from "@/api/echarts";
export default {
  name: "game",
  data() {
    return {
      stock: null,
      tdata: [],
      isClick: false,
      acval: null,
      alldata: [],
      arrnum: Math.floor(Math.random() * 360)
    };
  },
  created() {
    let that = this;
    let now = Date.parse(new Date());
    for (let i = 0; i < 420; i++) {
      let stamp = now - (360 - i) * 1000;
      let value = that.random();
      if (i >= 360) value = null;
      that.tdata.push({
        name: stamp,
        value: [stamp, value]
      });
    }
  },
  mounted() {
    let that = this;

    that.stock = echarts.init(that.$refs.chartEl);
    that.draw(that.tdata);

    setInterval(function() {
      let now = Date.parse(new Date());
      let after60s = now + 60 * 1000;
      let value = that.random();
      that.acval = value;

      that.tdata.shift();
      that.tdata.splice(-60, 1, {
        name: now,
        value: [now, value]
      });
      that.tdata.push({
        name: after60s,
        value: [after60s, null]
      });

      let MLdata = [];
      MLdata.push(
        {
          xAxis: now,
          lineStyle: {
            type: "dashed"
          },
          label: {
            position: "start",
            formatter: function(param) {
              return that.formatDate(param.value);
            }
          }
        },
        {
          yAxis: value,
          label: {
            position: "end"
          }
        }
      );

      let orders = JSON.parse(sessionStorage.getItem("orders"));
      if (orders) {
        orders.forEach((e, i) => {
          if (e.end > now) {
            let color, dir;
            switch (e.dir) {
              case 0:
                color = "#f36";
                dir = "➘";
                break;
              case 2:
                color = "#06c1c5";
                dir = "➚";
                break;
            }
            MLdata.push([
              {
                coord: [e.start, e.price],
                symbol: "roundRect",
                symbolSize: 8,
                lineStyle: {
                  color: color
                }
              },
              {
                coord: [e.end, e.price],
                symbol: "roundRect",
                symbolSize: 8,
                label: {
                  normal: {
                    show: true,
                    position: "top",
                    offset: [10, 330],
                    borderRadius: 3,
                    margin: 5,
                    padding: [3, 5],
                    backgroundColor: color,
                    formatter: dir
                  }
                }
              }
            ]);
          } else if (e.end == now) {
            orders.splice(i, 1);
            let result = 1;
            if (value > e.price) {
              result = 2;
            } else if (value < e.price) {
              result = 0;
            }
            if (e.dir == result) {
              that.$notify({
                message: "恭喜估中！",
                color: "#fff",
                background: "#06c1c5"
              });
              console.log("恭喜估中！");
            } else {
              that.$notify({
                message: "很遗憾，你没有估中！",
                color: "#fff",
                background: "#06c1c5",
              });
              console.log("很遗憾，你没有估中！");
            }
          }
        });
      }

      that.stock.setOption({
        series: [
          {
            data: that.tdata,
            markPoint: {
              data: [{ coord: [now, value] }]
            },
            markLine: {
              data: MLdata
            }
          },
          {
            data: [[now, value]]
          }
        ]
      });
    }, 1000);
  },
  methods: {
    buy(dir) {
      let that = this;

      that.isClick = true;
      setTimeout(function() {
        that.isClick = false;
      }, 888);

      let start = Date.parse(new Date());
      let end = start + 50 * 1000;
      let sessionOrders = JSON.parse(sessionStorage.getItem("orders"));
      if (!sessionOrders) sessionOrders = [];
      else if (sessionOrders.length >= 10) sessionOrders.shift();

      let newOrder = {
        start: start,
        end: end,
        dir: dir,
        price: that.acval
      };
      sessionOrders.push(newOrder);
      sessionStorage.setItem("orders", JSON.stringify(sessionOrders));
    },
    random() {
      let that = this;
      if (that.arrnum >= 360) that.arrnum = 0;
      let analog = that.$global.analog[that.arrnum];
      that.arrnum++;
      return analog;
    },
    formatDate(timestamp) {
      let date = new Date(timestamp);
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return h + ":" + m + ":" + s;
    },
    draw(data) {
      let that = this;
      let option = {
        grid: {
          top: 20,
          right: 90,
          bottom: 30,
          left: 24
        },
        dataZoom: [
          {
            type: "inside",
            start: 50,
            end: 100,
            zoomLock: true
          }
        ],
        tooltip: {
          showContent: false,
          triggerOn: "click",
          trigger: "none",
          axisPointer: {
            type: "cross",
            crossStyle: {
              type: "solid",
              color: "#333"
            },
            label: {
              fontSize: 18,
              margin: 5,
              padding: 3,
              borderRadius: 0,
              color: "#fff",
              backgroundColor: "#333",
              shadowBlur: 0,
              formatter: function(param) {
                if (param.axisDimension == "y") {
                  return param.value.toFixed(2);
                } else {
                  return that.formatDate(param.value);
                }
              }
            }
          }
        },
        series: [
          {
            data: that.tdata,
            type: "line",
            symbol: "none",
            markLine: {
              silent: true,
              symbol: "none",
              symbolSize: 5,
              animation: false,
              lineStyle: {
                type: "solid",
                width: 2
              },
              label: {
                normal: {
                  fontSize: 18,
                  padding: 3,
                  borderRadius: 0,
                  color: "#fff",
                  backgroundColor: "#333"
                }
              }
            }, //end markLine
            markPoint: {
              symbol: "diamond",
              symbolSize: 12,
              itemStyle: {
                color: "#333"
              }
            }, //end markPoint
            showSymbol: false,
            hoverAnimation: false,
            color: "#333",
            width: 2
          },
          {
            //这里是涟漪的点用的
            type: "effectScatter",
            symbol: "diamond",
            symbolSize: 5,
            rippleEffect: {
              scale: 10,
              period: 3,
              brushType: "fill"
            },
            itemStyle: {
              color: "#999"
            }
          }
        ], //end series
        xAxis: {
          type: "time",
          splitNumber: 3,
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false,
            color: "#333",
            fontSize: 18,
            showMinLabel: false,
            showMaxLabel: false,
            align: "left",
            formatter: function(value) {
              let date = new Date(1 * value);
              let h = date.getHours();
              h = h < 10 ? "0" + h : h;
              let m = date.getMinutes();
              m = m < 10 ? "0" + m : m;
              let s = date.getSeconds();
              s = s < 10 ? "0" + s : s;
              return h + ":" + m + ":" + s;
            }
          },
          label: {
            backgroundColor: "#333",
            shadowBlur: 0,
            borderRadius: 0
          },
          axisPointer: {
            snap: false
          }
        },
        yAxis: {
          type: "value",
          position: "right",
          splitNumber: 3,
          scale: true,
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false
          },
          boundaryGap: ["10%", "10%"]
        }
      };
      that.stock.setOption(option, true);
    }
  }
};
</script>