<template>
  <div class="about">
    <div
      class="ab-item"
      v-for="(item, i) in list"
      :key="i"
      :style="'background-image: url(' + item.bg + ');background-position: center ' + reBgPY(i) + 'px'"
    >
      <div class="ab-content">这里是内容哦{{i}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      bgTop: 0,
      winH: document.documentElement.clientHeight,
      list: [
        { bg: require("../assets/img/ab_01.jpg") },
        { bg: require("../assets/img/ab_02.jpg") },
        { bg: require("../assets/img/ab_03.jpg") },
        { bg: require("../assets/img/ab_05.jpg") }
      ]
    };
  },
  methods: {
    handScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.bgTop = scrollTop;
    },
    reBgPY(i) {
      return this.bgTop - i * this.winH;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handScroll);
  }
};
</script>